.header {
    width: 100%;
    height: 60px;
    display: flex;
    background: #484059;
    padding: 0 200px;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 1000;
}
.header-left {
    display: flex;
    height: 100%;
    align-items: center;
}
.header-left h4 {
    font-size: 25px;
    color: #fff;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.header-right {
    height: 100%;
    align-items: center;
}
.header-right ul {
    list-style: none;
    display: flex;
    height: 100%;
    align-items: center;
}
.header-right ul li a{
    color: #fff;
    text-decoration: none;
    margin-right: 30px;
    font-size: 18px;
    position: relative;
    display: flex;
    padding-bottom: 4px;
}
.underline_m {
    width: 100%;
    position: absolute;
    height: 2px;
    left: 0;
    bottom: 0;
    background: #484059;
    -webkit-transform-origin: left;
            transform-origin: left;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    transition: all .3s ease;
}
.header-right ul li a:hover .underline_m{
    background: #fff;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}



.header_responsive {
    display: none;
    position: fixed;
    background: #484059d2;
    top: 60px;
    left: -100vw;
    transition: all .3s ease;
    width: 100%;
    height: calc(100vh - 60px);
}
.h_r_s {
    left: 0;
}
.h_r_h {
    left: -100vw;
}

.header_responsive ul {
    display: flex;
    list-style: none;
    flex-direction: column;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: space-evenly;
}
.header_responsive ul li a {
    text-decoration: none;
    font-size: 22px;
    color: #fff;
}

.buttons {
    height: 100%;
    display: none;
    align-items: center;
    justify-content: center;
}
.buttons button {
    outline: none;
    border: none;
    cursor: pointer;
    background: transparent;
}

.close_icon, .menu_icon {
    color: #fff;
    font-size: 28px!important;
}




